.App {
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(255, 163, 240);
  min-height: 100vh;
  
}

.App > div {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  max-width: 340px; 
  width: 100%; 
  padding: 20px; 
  background: white;
  border: 3px solid white;
  border-radius: 10px;
  color:rgb(253, 62, 221);
  font-size: 25px;
  box-shadow: 0 7px 15px rgba(0, 0, 0, 1);
}

.btnReset{
  margin: 5px;

}



.delete-button {
  position: relative;
  width: 30px;
  height: 30px;
  background-color: rgb(253, 62, 221);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  margin-left: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s, box-shadow 0.2s;
}

.delete-button:hover {
  transform: scale(1.1);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

.delete-button:hover .cross::before,
.delete-button:hover .cross::after {
  background-color: #fff5f5;
}




.add-button {
  position: relative;
  width: 30px;
  height: 30px;
  background-color: rgb(253, 62, 221);
  border: none;
  border-radius: 10%;
  cursor: pointer;
  margin-left: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s, box-shadow 0.2s;
}

.add-button:hover {
  transform: scale(1.1);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

.add-button:hover .cross::before,
.add-button:hover .cross::after {
  background-color: #fff5f5;
}




.custom-checkbox {

  width: 20px;
  height: 20px;
  margin: 10px;
}










.inputBtn{
  width: 200px;
  height: 24px;
  margin-bottom: 18px;
}



.error {
  border: red 1px solid;
}
 
.error-message {
  color: red;
}




.button {
  padding: 10px 20px;
  background-color: rgb(255, 140, 236);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 15px;
  margin: 25px 0 0 5px
}

.active-filter {
  background-color: rgb(253, 62, 221);
}


.is-done {
  opacity: 0.5;
}


